$(document).ready(function ($) {
    //menu
    $('.gamburger').on('click', () => {
        $('.menu').toggleClass('opened');
        $('.gamburger').toggleClass('close-menu');
        $('.close').toggleClass('open');
    });
    $('.close').on('click', () => {
        $('.menu').toggleClass('opened');
        $('.gamburger').toggleClass('close-menu');
        $('.close').toggleClass('open');
    });

    // Menu Scroll to section

    $('a[href^="#"').click(function () {
        let target = $(this).attr('href');
        $('html, body').animate({
            scrollTop: $(target).offset().top
        }, 1500);

        return false;
    });

    // scroll up

    $(window).on('scroll', () => {
        if ($(this).scrollTop() > 250) {
            $('.toTop').fadeIn();
        }else {
            $('.toTop').fadeOut();
        }
    });


    //Modal form
    $('.button').click(function (e) {
        e.preventDefault();
        
    });

    //slider
    let mySwiper = new Swiper('.swiper-container', {
        // Optional parameters
        direction: 'vertical',
        loop: true,
      
        // If we need pagination
        pagination: {
          el: '.swiper-pagination',
        },
      
        // Navigation arrows
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev',
        }
      
      });

    // form validation

    $(document).ready(function() {
        $('[data-submit]').on('click', function(e) {
            e.preventDefault();
            $(this).parent('form').submit();
        })
        $.validator.addMethod(
            "regex",
            function(value, element, regexp) {
                var re = new RegExp(regexp);
                return this.optional(element) || re.test(value);
            },
            "Please check your input."
        );



        //     
        function valEl(el) {

            el.validate({
                rules: {
                    tel: {
                        required: true,
                        regex: '^([\+]+)*[0-9\x20\x28\x29\-]{5,20}$'
                    },
                    name: {
                        required: true
                    },
                    email: {
                        required: true,
                        email: true
                    },
                    checkbox: {
                        required: true
                    }
                },
                messages: {
                    tel: {
                        required: ' ',
                        regex: '    + - ()'
                    },
                    name: {
                        required: ' ',
                    },
                    email: {
                        required: ' E-mail',
                        email: '  E-mail'
                    },
                    checkbox: {
                        required: '    '
                    }
                },

                //   id="" 
                submitHandler: function(form) {
                    $('#loader').fadeIn();
                    let $form = $(form);
                    let $formId = $(form).attr('id');
                    switch ($formId) {
                        //    id="goToNewPage" - :
                        case 'goToNewPage':
                            $.ajax({
                                type: 'POST',
                                url: $form.attr('action'),
                                data: $form.serialize(),
                            })
                                .always(function(response) {
                                    //   "" - 
                                    location.href = '';
                                });
                            break;
                        //    id="popupResult" - :
                        case 'popupResult':
                            $.ajax({
                                type: 'POST',
                                url: $form.attr('action'),
                                data: $form.serialize(),
                            })
                                .always(function(response) {
                                    setTimeout(function() {
                                        $('#loader').fadeOut();
                                    }, 800);
                                    setTimeout(function() {
                                        $('#overlay').fadeIn();
                                        $form.trigger('reset');
                                        //     .  Google Analytics
                                    }, 1100);
                                    $('#overlay').on('click', function(e) {
                                        $(this).fadeOut();
                                    });
                                    $('#exampleModal').arcticmodal('close');

                                });
                            break;
                    }
                    return false;
                }
            })
        }

        //    ,      .js-form
        $('.js-form').each(function() {
            valEl($(this));
        });

    });

});